.products-section-container {
    padding-top: 80px;
    padding-bottom: 40px;

    @include tablet {
        padding-top: 40px;
        padding-bottom: 60px;
    }
    @include desktop {
        padding-top: 60px;
    }
}

.products-top-text {
    margin-bottom: 15px;
    font-family: Titan One;
    font-size: 18px;
    line-height: 1.67;
    text-align: center;
    text-transform: uppercase;
    color: var(--pink-color);

    @include tablet {
        margin-bottom: 18px;
        font-size: 26px;
        line-height: 1.15;
    }

   @include desktop {
        margin-bottom: 16px;
        font-size: 30px;
        line-height: 1.13;
        letter-spacing: 0.04em;
    }
}
.products-title {
    margin-bottom: 148px;
    font-family: Titan One;
    font-weight: normal;
    font-size: 34px;
    line-height: 1.15;
    letter-spacing: 0.04em;
    text-align: center;
    text-transform: uppercase;
    color: var(--accent-color);

    @include tablet {
        margin-bottom: 154px;
        font-size: 48px;
    }
   @include desktop {
        margin-bottom: 258px;
        font-size: 58px;
        line-height: 1.14;
        letter-spacing: 0.06em;
    }
}
.products-list {
    text-align: center;

    @media screen and (max-width: 768px) {
        margin-bottom: -140px;
    }

    @include tablet {
    display: flex;
    justify-content: center;
    text-align: center;
    margin-left: -16px;
    }
    @include desktop {
        margin-left: -32px;
    }
}


.products-item {
    position: relative;
    width: 280px;
    padding: 160px 40px 40px;
    margin-left: auto;
    margin-right: auto;
    border-radius: 24px;
    @media screen and (max-width: 768px) {
        margin-bottom: 140px;
    }
   
    @include tablet {
        width: calc(100% / 3 - 16px);
        margin-left: 16px;
        padding: 118px 32px 40px;
    }
    @include desktop {
        width: calc(100% / 3 - 32px);
        margin-left: 32px;
        padding: 165px 77px 60px;
    }
}

.products-item-cream {
    background-color: #ffb8ca;
}

.products-item-cream::before {
    content: "";
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    width: 280px;
    height: 280px;
    transform: translateY(-50%);
    background-image: url(../images/Products/products-mob/prod1-mob1x.webp);
    background-size: contain;
    background-repeat: no-repeat;
    
    @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
            background-image: url(../images/Products/products-mob/prod1-mob2x.webp);
        }

    @include tablet {
        width: 224px;
        height: 224px;
        transform: translateY(-67%);
        background-image: url(../images/Products/products-tab/prod1-tab1x.webp);
        @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
            background-image: url(../images/Products/products-tab/prod1-tab2x.webp);
        }
    }
    @include desktop {
        width: 395px;
        height: 395px;
        background-image: url(../images/Products/products-dt/prod1-dt1x.webp);
        @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
            background-image: url(../images/Products/products-dt/prod1-dt2x.webp);
        }
    }
}

