.products-item-title {
    position: relative;
    margin-bottom: 70px;
    font-family: Titan One;
    font-weight: normal;
    font-size: 20px;
    line-height: 1.15;
    letter-spacing: 0.04em;
    text-align: center;
    text-transform: uppercase;
    color: var(--white-color);

    @include desktop {
        font-size: 30px;
        line-height: 1.13;
    }
}

.products-item-title::after {
    content: "";
    position: absolute;
    display: block;
    bottom: -32pt;
    left: 50%;
    width: 26px;
    height: 6px;
    transform: translateX(-50%);
    background-image: url(../images/Products/products-points/products-points-1x.webp);
    background-size: contain;
    background-repeat: no-repeat;
    @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
            background-image: url(../images/Products/products-points/product-points-2x.webp);
        }
}

.products-text {
    margin-bottom: 32px;
    font-weight: bold;
    font-size: 14px;
    line-height: 1.52;
    letter-spacing: 0.04em;
    text-align: center;
    color: var(--white-color);

    @include tablet {
        line-height: 1.76;
    }
    @include desktop {
        margin-bottom: 60px;
        font-size: 16px;
        line-height: 1.76;
    }
}

.products-btn {
    width: 40px;
    height: 40px;
    border: none;
    border-radius: 50%;
    background-color: var(--white-color);
    transition-property: background-color;
    transition-duration: var(--transition-duration);
    transition-timing-function: var(--transition-timing);
   cursor: pointer;
   &:hover,
   &:focus {
    background-color: var(--button-white-hover);
}
}
.arrow {
    fill: var(--accent-color);
}
.products-description-container {
    position: absolute;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;
    width: 80%;
    height: 42%;
    bottom: 90px;
    left: 50%;
    transform: translateX(-50%);
    overflow-y: scroll;
     transition: 500ms opacity linear;

     @include tablet {
        bottom: 110px;
    }
    @include desktop {
        height: 45%;
    }
}
.ice-container {
    background-color: #FFA5BA; 
}
.coffee-container {
    background-color: #e0b982;
}
.shake-continer {
    background-color: #a6cc74;
}

.description-img {
    width: 150px;
    height: 150px;

    @include desktop {
        width: 220px;
        height: 220px;
    }

}

.products-description-text {
    font-size: 14px;
    line-height: 1.52;
    letter-spacing: 0.04em;
    text-align: center;
    color: var(--white-color);
}
.products-description-price {
    font-size: 24px;
    line-height: 1.52;
    letter-spacing: 0.04em;
    text-align: center;
    color: var(--accent-color);
}

.products-btn-cream .products-description-container {
    opacity: 0;
  }
  .products-btn-cream.is-open .products-description-container {
    opacity: 1;
  }
  .products-btn-coffee .products-description-container {
    opacity: 0;
  }
  .products-btn-coffee.is-open .products-description-container {
    opacity: 1;
  }
  .products-btn-milkshakes .products-description-container {
    opacity: 0;
  }
  .products-btn-milkshakes.is-open .products-description-container {
    opacity: 1;
  }